export const AddCart = (payload: string) => ({
  type: 'ADDCART',
  payload
});

export const DeleteCart = (payload: string) => ({
  type: 'DELETECART',
  payload
});

export const DeleteAllItemCart = (payload: string) => ({
  type: 'DELETEALLITEMCART',
  payload
});

export const DeleteAllCart = () => ({
  type: 'DELETEALLCART'
});

export const SetCart = (payload: string) => ({
  type: 'SETCART',
  payload
});
